.container-menu {
  max-width: 41rem;
  max-height: 4.375rem;
  margin: 0 auto;
}
@media (min-width: 40em) {
  .container-menu {
    padding: 0;
  }
}

.nav-wrap {
  overflow: visible;
  height: 2.625rem;
  width: 27.1875rem;
  z-index: 30;
  position: relative;
  overflow: hidden;
  display: flex;
}
@media (max-width: 46.875em) {
  .nav-wrap {
    display: table;
    height: 15.125rem;
    width: 100%;
    position: relative;
    top: 0%;
    padding: 0;
    margin: 0%;
  }
}

.menu-dropbtn {
  position: absolute;
  z-index: 40;
  display: none;
}
.menu-dropbtn img {
  display: none;
  visibility: hidden;
}
@media (max-width: 46.875em) {
  .menu-dropbtn {
    background: transparent;
    border: none;
    display: block;
    width: 100%;
    height: 2.5625rem;
  }
  .menu-dropbtn img {
    visibility: visible;
    position: absolute;
    top: 0.4375rem;
    left: 0.625rem;
    display: block;
    visibility: visible;
    height: 1.5625rem;
    width: 1.5625rem;
  }
}

.nav-right {
  width: 21.875rem;
  height: 2.1875rem;
  background-color: #7491b4;
  top: 0%;
  left: 50%;
  transform: translateX(24%);
  z-index: 30;
}
@media (max-width: 46.875em) {
  .nav-right {
    height: 2.5625rem;
    transform: none;
    left: 0;
    width: 100%;
    position: relative;
    display: flex;
  }
  .nav-right .container__nav--label {
    flex-direction: column;
  }
  .nav-right img {
    visibility: visible;
    display: inline-block;
    position: relative;
    left: 0.625rem;
    top: 0.5625rem;
    background-color: transparent;
  }
}

@media (max-width: 46.875em) {
  .nav-right.show {
    height: 15.125rem;
  }
}

.nav-left {
  position: absolute;
  display: block;
  width: 2.1875rem;
  height: 2.1875rem;
  background-color: #A68359;
  top: 0;
  left: 50%;
  transform: translateX(-10.5rem);
  z-index: 30;
}
.nav-left span {
  display: none;
}
@media (max-width: 46.875em) {
  .nav-left {
    display: none;
  }
}

.nav-left:hover {
  background-color: #c3a683;
}

@media (max-width: 46.875em) {
  .nav-left.show {
    display: block;
    width: 100%;
    position: absolute;
    height: 2.4375rem;
    left: 0;
    top: 2.5625rem;
    transform: none;
    z-index: 200;
  }
  .nav-left.show span {
    display: block;
  }
}

.nav-left img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 46.875em) {
  .nav-left img {
    visibility: collapse;
  }
}

.nav--label--table {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: start;
  text-decoration: none;
}
.nav--label--table span {
  padding: 0.625rem 0.625rem;
  text-align: center;
  color: #000;
  font-family: "Timeburner", sans-serif;
  font-size: 0.9375rem;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
  text-decoration: none;
  visibility: collapse;
}
@media (max-width: 46.875em) {
  .nav--label--table span {
    visibility: visible;
  }
}

.container__nav--label {
  display: flex;
  position: absolute;
  align-items: center;
  justify-content: center;
}
@media (max-width: 46.875em) {
  .container__nav--label {
    position: absolute;
    display: none;
    width: 100%;
    margin-top: 5rem;
    text-align: left;
    align-items: left;
    justify-content: flex-start;
  }
}

.nav--label {
  padding: 0.390625rem 1.28125rem;
  text-align: center;
  color: #000;
  font-family: "Timeburner", sans-serif;
  font-size: 0.9375rem;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
  text-decoration: none;
}
@media (max-width: 46.875em) {
  .nav--label {
    height: 2.5rem;
    width: 100%;
    text-align: left;
    justify-content: left;
    margin: auto;
    padding: 0.625rem 0.625rem;
  }
}

@media (max-width: 46.875em) {
  .container__nav--label.show {
    display: flex;
  }
}

#nav-item-separator {
  display: none;
}
@media (max-width: 46.875em) {
  #nav-item-separator {
    height: 0.03125rem;
    width: 100%;
    all: unset;
    display: block;
    background-color: black;
    z-index: 100;
  }
}

.nav--label:hover {
  text-decoration: underline;
  line-height: 150%;
  background-color: #89a2c0;
  height: 2.1875rem;
}
@media (max-width: 46.875em) {
  .nav--label:hover {
    height: 2.5rem;
    line-height: 150%;
  }
}

