.studio-logo1 {
  position: relative;
  align-items: center;
  left: 50%;
  transform: translate(-16.28125rem, 5.625rem);
  overflow: hidden;
  height: 20rem;
  aspect-ratio: 288/157;
  z-index: 20;
}
@media (max-width: 39.9375em) {
  .studio-logo1 {
    left: 0;
    transform: none;
    position: relative;
    height: unset;
    max-height: 12.5rem;
    object-fit: contain;
    width: 100%;
    padding-top: 0;
  }
}
@media (max-width: 26.875em) {
  .studio-logo1 {
    left: 0;
    transform: none;
    position: relative;
    height: unset;
    max-height: 12.5rem;
    object-fit: contain;
    width: 100%;
    padding-top: 0;
  }
}

.studio-logo-background {
  min-height: 31.25rem;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: clip;
  filter: brightness(1.5);
  object-fit: fill;
  z-index: 0;
}
@media (max-width: 46.875em) {
  .studio-logo-background {
    object-fit: unset;
    position: relative;
    min-height: 12.5rem;
    height: 100%;
    width: 100vw;
  }
}

